body {
  font-family: Arial, sans-serif;
  margin: 0;
  padding: 0;
  background-color: #f4f4f4;
}

.app {
  max-width: 1200px;
  margin: 0 auto;
  padding: 20px;
}

.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #333;
  color: white;
  padding: 10px 20px;
}

.navbar-logo {
  display: flex;
  align-items: center;
  gap: 10px;
  font-size: 1.2em;
}

.navbar-toggle {
  position: relative;
}

.navbar-checkbox {
  display: none;
}

.navbar-hamburger {
  display: none;
  flex-direction: column;
  gap: 5px;
  cursor: pointer;
  padding: 5px;
}

.navbar-hamburger span {
  width: 25px;
  height: 3px;
  background-color: white;
  transition: all 0.3s ease;
}

.navbar-checkbox:checked ~ .navbar-hamburger span:nth-child(1) {
  transform: rotate(45deg) translate(5px, 5px);
}

.navbar-checkbox:checked ~ .navbar-hamburger span:nth-child(2) {
  opacity: 0;
}

.navbar-checkbox:checked ~ .navbar-hamburger span:nth-child(3) {
  transform: rotate(-45deg) translate(7px, -7px);
}

.navbar-menu {
  display: flex;
  list-style: none;
  margin: 0;
  padding: 0;
  gap: 20px;
}

.navbar-menu li a {
  color: white;
  text-decoration: none;
}

.navbar-menu li a:hover {
  text-decoration: underline;
}

@media (max-width: 768px) {
  .navbar-hamburger {
    display: flex;
  }

  .navbar-menu {
    display: none;
    position: absolute;
    top: 60px;
    right: 20px;
    background-color: #333;
    flex-direction: column;
    padding: 10px;
    border-radius: 5px;
    box-shadow: 0 2px 5px rgba(0,0,0,0.3);
    transition: all 0.3s ease;
  }

  .navbar-checkbox:checked ~ .navbar-menu {
    display: flex;
  }
}

.match, .set, .game, .tiebreak, .match-history, .stats, .matches, .import-matches {
  background: white;
  padding: 20px;
  margin-bottom: 20px;
  border-radius: 5px;
  box-shadow: 0 2px 5px rgba(0,0,0,0.1);
}

.match h2, .set h4, .game h5, .tiebreak h5 {
  display: flex;
  align-items: center;
  gap: 10px;
}

.fold-button {
  background: none;
  background-color: #007bff;
  border: none;
  font-size: .75em;
  cursor: pointer;
  padding: 0 5px;
  transition: transform 0.2s ease;
}

.fold-button:hover {
  transform: scale(1.2);
}

button {
  padding: 8px 16px;
  margin: 5px;
  border: none;
  border-radius: 4px;
  background-color: #007bff;
  color: white;
  cursor: pointer;
}

button:hover {
  background-color: #0056b3;
}

button:disabled {
  background-color: #ccc;
  cursor: not-allowed;
}

button.positive-button {
  background-color: #28a745;
}

button.positive-button:hover {
  background-color: #218838;
}

button.negative-button {
  background-color: #dc3545;
}

button.negative-button:hover {
  background-color: #c82333;
}

button.selected {
  background-color: #ffc107;
  color: black;
}

.set-creation {
  margin-top: 10px;
  display: flex;
  gap: 10px;
}

.point-buttons {
  display: flex;
  flex-direction: row;
  gap: 20px;
  flex-wrap: wrap;
}

.positive-outcomes, .negative-outcomes {
  flex: 1;
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
}

.positive-outcomes button, .negative-outcomes button {
  flex: 0 0 auto;
  min-width: 100px;
}

.point-history-table, .matches-table {
  width: 100%;
  border-collapse: collapse;
  margin-top: 10px;
}

.point-history-table th, .point-history-table td, .matches-table th, .matches-table td {
  border: 1px solid #ddd;
  padding: 8px;
  text-align: left;
}

.point-history-table th, .matches-table th {
  background-color: #f2f2f2;
}

.outcome-positive {
  color: green;
}

.outcome-negative {
  color: red;
}

.modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0,0,0,0.5);
  display: flex;
  align-items: center;
  justify-content: center;
}

.modal-content {
  background: white;
  padding: 20px;
  border-radius: 5px;
  max-width: 500px;
  width: 100%;
}

.modal-content label {
  display: block;
  margin-bottom: 10px;
}

.modal-content input, .modal-content select {
  width: 100%;
  padding: 8px;
  margin-top: 5px;
}

.button-group {
  display: flex;
  gap: 10px;
  flex-wrap: wrap;
}

form {
  display: flex;
  flex-direction: column;
  gap: 10px;
}

form label {
  display: flex;
  flex-direction: column;
}


.match-actions, .set-actions, .game-actions, .tiebreak-actions {
  display: flex;
  gap: 10px;
  flex-wrap: wrap;
}

/* unused */
.buttons-container {
  display: flex;
  flex-direction: row;
  gap: 5px; /* Reduced from 10px */
  width: 100%; /* Full width of parent */
  padding: 5px; /* Reduced from 10px */
  box-sizing: border-box;
}

.action-btn {
  display: flex;
  align-items: center; /* Center icon and text vertically */
  justify-content: center; /* Center content horizontally */
  padding: 5px; /* Reduced from 10px */
  font-size: 12px; /* Reduced from 16px */
  color: #fff; /* White text */
  border: none;
  border-radius: 3px; /* Reduced from 5px */
  cursor: pointer;
  transition: background-color 0.2s; /* Smooth hover effect */
}

/* Specific styles for each button */
.edit-btn {
  background-color: #4CAF50; /* Green for edit */
}
.edit-btn:hover {
  background-color: #45a049;
}

.delete-btn {
  background-color: #f44336; /* Red for delete */
}
.delete-btn:hover {
  background-color: #da190b;
}

.save-btn {
  background-color: #2196F3; /* Blue for save */
}
.save-btn:hover {
  background-color: #1e88e5;
}

.export-btn {
  background-color: #ff9800; /* Orange for export */
}
.export-btn:hover {
  background-color: #e68a00;
}

.log-btn {
  background-color: #9c27b0; /* Purple for log */
}
.log-btn:hover {
  background-color: #8e24aa;
}

.finish-btn {
  background-color: #26a69a; /* Teal for finish */
}
.finish-btn:hover {
  background-color: #00897b;
}

.load-btn {
  background-color: #00bcd4; /* Cyan for load */
}
.load-btn:hover {
  background-color: #00acc1;
}

.history-btn {
  background-color: #607d8b; /* Gray for history */
}
.history-btn:hover {
  background-color: #546e7a;
}

.stats-btn { 
  background-color: #3f51b5;
} /* Dark blue for stats */
.stats-btn:hover { 
  background-color: #3949ab; 
}

/* Responsive design: Stack buttons on small screens */
@media (max-width: 600px) {
  .buttons-container .match-actions, .set-actions, .game-actions, .tiebreak-actions {
    flex-direction: column;
  }
  .action-btn {
    width: 100%; /* Full width on mobile */
  }
}